body {
  /* margin: 10; */
  /* padding: 0px 0; */
  /* width: 80%;
  margin: auto; */
  font-family: Helvetica, Arial, sans-serif;
}

@media (min-width: 768px) {

  body {
    width: 80%;
    margin: 0 auto;
  }
  .header li {
    float: left;
  }

}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}

.form-input,
.form-output {
  background-color: #eee;
  padding: 10px;
}

.header {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #6A5ACD;
}


li a {
  display: block;
  color: white;
  text-align: center;
  padding: 16px;
  text-decoration: none;
}

li a:hover {
  background-color: #cbc5ee;
  color: black;
}

.active {
  color: black;
  background-color: #cbc5ee;    
}
h2 {
  text-align: center;
  margin: 20px 0;
}
p {
  margin: 10px 20px;
}

.menu-book {
  border-bottom: 2px solid #000;
  /* border-top: 1px solid #000; */
  padding-bottom: 2rem;
  padding-top: 2rem;
  margin-bottom: 5px;
  clear: both;
  overflow: hidden;
}

.menu-book .book-name {
  margin: 0;
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
          align-items: center;
}

.menu-book img {
  float: left;
  width: 150px;
  margin-right: 1rem;

  
}

.menu-book .price {
  font-size: 1.4rem;
  -webkit-box-pack: end;
  justify-content: flex-end;
}

.order-wrap {
  width: 500px;
  height: 400px;
  
  border: 3px solid black;
}
.order-list {
  list-style: none;
  
}
.book-store{
  height: 90vh;
  display: flex;
}

.books{
  overflow-y: scroll;
  scrollbar-width: none;
  width: 50vw;
  padding: 0 50px;
  
}
.books::-webkit-scrollbar{
  display: none;
}
.order-items {
  position: relative;
}
.order-price {
  position: absolute;
  right: 50px;
}
.delete-order {
  position: absolute;
  right: 10px;
}

.total {
  margin: 0 40px;
}